<template>
  <div class="wrapper">
    <div class="content1">
      <div v-for="(item, index) in code" :key="index">
        <div class="head1" style="font-size: 22px">
          <h1>{{ item.title }}</h1>
          <h2 v-if="remarkShow(item.remark)">({{ item.remark }})</h2>
        </div>
        <Card>
          <div class="point1">
            <ul>
              <li v-for="(item1, index1) in item.codeContent" :key="index1">
                {{ item1 }}
              </li>
            </ul>
          </div>
        </Card>
      </div>
    </div>
  </div>
</template>

<script>
import codeList from "../../js/codes.js";
export default {
  name: "Codes",
  data() {
    return {
      name: `jsx`,
      str: `${["jsx", "ljj"].map((a) => `${a}`)}`,
      code: [],
    };
  },
  computed: {
    remarkShow() {
      return function (str) {
        if (str) {
          return str.length != 0;
        } else {
          return false;
        }
      };

      //  return (str) => str.length != 0
    },
  },
  created() {
    console.log(this);
    this.code = codeList.data.codeList;
  },
  mounted() {
    // console.log(`${this.name}`);
    // console.log(`${this.fn()}`);
    // console.log(`${this.str}`);
  },
  methods: {
    fn() {
      return "hello";
    },
  },
};
</script>

<style>
* {
  box-sizing: border-box;
}
.content1,
.content2 {
  width: 1200px;
  margin: 0 auto;
}
.head1,
.head2 {
  width: 100%;
  text-align: center;
}
.point1,
.point2 {
  font-size: 26px;
  padding: 12px;
}
</style>



